<template>
  <div class="flex justify-center items-center min-h-screen bg-gray-100">
    <n-card class="w-[320px] shadow-xl rounded-2xl" :title="`Âç°Áâá ${currentIndex + 1}`">
      <!-- ÊòæÁ§∫ÈóÆÈ¢ò -->
      <div class="text-lg font-semibold text-center min-h-[60px]">
        {{ currentCard.question }}
      </div>

      <!-- Èü≥È¢ëÊí≠ÊîæÊåâÈíÆ -->
      <div class="mt-4 text-center">
        <n-button @click="playAudio" secondary type="success">
          Êí≠ÊîæÈü≥È¢ë
        </n-button>
      </div>

      <!-- ÊòæÁ§∫Á≠îÊ°à -->
      <div v-if="showAnswer" class="text-green-600 mt-4 text-center">
        Á≠îÊ°àÔºö{{ currentCard.answer }}
      </div>

      <!-- ÊéßÂà∂ÊåâÈíÆ -->
      <div class="mt-6 flex justify-between">
        <n-button @click="toggleAnswer" secondary type="info">
          {{ showAnswer ? 'ÈöêËóèÁ≠îÊ°à' : 'ÊòæÁ§∫Á≠îÊ°à' }}
        </n-button>
        <n-button @click="nextCard" secondary type="primary">
          ‰∏ã‰∏Ä‰∏™
        </n-button>
      </div>
      <!-- ÈöêËóèÁöÑ audio ÂÖÉÁ¥† -->
      <audio ref="audioRef" :src="currentCard.audio" preload="auto"></audio>
    </n-card>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { NCard, NButton } from 'naive-ui'

// üëâ Âè™Áª¥Êä§ mp3 Êñá‰ª∂ÂêçÔºàÊó†ÈúÄÂÜôÈóÆÈ¢ò„ÄÅÁ≠îÊ°àÔºâ
const filenames = [
  'hello.mp3',
  'pantou.mp3',
  'anun.mp3',
  'buxing.mp3'
]

// Ëá™Âä®Ê†πÊçÆÊñá‰ª∂ÂêçÁîüÊàêÂç°Áâá
function filenameToCard(name) {
  const displayName = name.replace('.mp3', '')
  return {
    question: `ÂÖ¥ÂåñÊñπË®ÄÔºö${displayName}`, // ‰Ω†‰πüÂèØ‰ª•ÊääËøôÈáåÊõøÊç¢Êàê‰∏≠ÊñáÊ≥®ÈáäÊàñÂ≠óÂÖ∏Êò†Â∞Ñ
    answer: '', // ÂèØÁïôÁ©∫Êàñ‰ª•ÂêéÂä†ÂÖ•Â≠óÂÖ∏Êò†Â∞Ñ
    audio: `/audio/${name}`
  }
}

const cards = ref(filenames.map(filenameToCard))

// ÂΩìÂâçÊí≠ÊîæÁöÑÁ¥¢ÂºïÂíåÁä∂ÊÄÅ
const currentIndex = ref(Math.floor(Math.random() * cards.value.length))
const showAnswer = ref(false)
const audioRef = ref(null)

const currentCard = computed(() => cards.value[currentIndex.value])

function toggleAnswer() {
  showAnswer.value = !showAnswer.value
}

function nextCard() {
  const randomIndex = Math.floor(Math.random() * cards.value.length)
  currentIndex.value = randomIndex
  showAnswer.value = false
}

function playAudio() {
  if (audioRef.value) {
    audioRef.value.currentTime = 0
    audioRef.value.play()
  }
}
</script>
